"use client";

import { useParams } from "next/navigation";
import { useEffect, useMemo, useState } from "react";
import {
  ArrowPathIcon,
  ExclamationTriangleIcon,
  DocumentMagnifyingGlassIcon,
  ClipboardIcon,
  LightBulbIcon,
  ChatBubbleLeftRightIcon,
  SparklesIcon,
  MagnifyingGlassIcon,
  FunnelIcon,
} from "@heroicons/react/24/outline";

/* ================= TYPES ================= */

type ChangeType = "ADDED" | "REMOVED" | "MODIFIED";
type RiskLevel = "LOW" | "MEDIUM" | "HIGH";
type AiMode = "short" | "detailed" | "legal" | "risk";

type ChangeItem = {
  id: number;
  change_type: ChangeType;
  section_label: string | null;
  old_text: string | null;
  new_text: string | null;
  risk_level?: RiskLevel | null;
  ai_comment?: string | null;
  ai_suggestion?: string | null;
  risk_reason?: string | null;
};

type ComparisonDetail = {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: string | null;
  summary_text?: string | null;
  changes: ChangeItem[];
};

type ChatState = {
  open: boolean;
  mode: AiMode;
  question: string;
  loading: boolean;
  answer: string;
  error: string | null;
};

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() || "http://127.0.0.1:8000";

/* ================= HELPERS ================= */

function normalizeText(s?: string | null) {
  return (s || "").toString().trim();
}

function contains(hay: string, needle: string) {
  return hay.toLowerCase().includes(needle.toLowerCase());
}

function formatDateTime(iso: string) {
  try {
    return new Date(iso).toLocaleString("th-TH", {
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  } catch {
    return iso;
  }
}

function riskBadge(risk?: string | null) {
  const base = "px-2 py-0.5 rounded text-xs font-bold";
  const level = (risk || "LOW").toUpperCase();

  if (level === "HIGH")
    return <span className={`${base} bg-red-100 text-red-700 border border-red-300`}>HIGH</span>;
  if (level === "MEDIUM")
    return <span className={`${base} bg-amber-100 text-amber-700 border border-amber-300`}>MEDIUM</span>;
  return <span className={`${base} bg-emerald-100 text-emerald-700 border border-emerald-300`}>LOW</span>;
}

function changeTypeBadge(type: string) {
  const base = "px-2 py-0.5 rounded text-xs font-bold";
  if (type === "ADDED")
    return <span className={`${base} bg-emerald-100 text-emerald-700 border border-emerald-300`}>ADDED</span>;
  if (type === "REMOVED")
    return <span className={`${base} bg-red-100 text-red-700 border border-red-300`}>REMOVED</span>;
  return <span className={`${base} bg-amber-100 text-amber-700 border border-amber-300`}>MODIFIED</span>;
}

async function copyToClipboard(text: string) {
  try {
    await navigator.clipboard.writeText(text);
    return true;
  } catch {
    try {
      const el = document.createElement("textarea");
      el.value = text;
      document.body.appendChild(el);
      el.select();
      document.execCommand("copy");
      document.body.removeChild(el);
      return true;
    } catch {
      return false;
    }
  }
}

function CopyButton({ text, label = "Copy", size = "sm" }: {
  text: string;
  label?: string;
  size?: "sm" | "md";
}) {
  const disabled = !text || text.trim().length === 0;
  return (
    <button
      type="button"
      disabled={disabled}
      onClick={async () => await copyToClipboard(text)}
      className={`
        inline-flex items-center gap-1 ${size === "sm" ? "px-2 py-1 text-xs" : "px-3 py-1.5 text-sm"}
        rounded border font-medium
        ${disabled
          ? "opacity-50 cursor-not-allowed bg-gray-100 text-gray-400 border-gray-300"
          : "bg-white text-gray-700 border-gray-300 hover:bg-gray-50 hover:border-gray-400"
        }
      `}
    >
      <ClipboardIcon className="h-3.5 w-3.5" />
      {label}
    </button>
  );
}

function DiffView({ oldText, newText }: { oldText: string; newText: string }) {
  const oldWords = oldText.split(/(\s+)/);
  const newWords = newText.split(/(\s+)/);
  const oldSet = new Set(oldWords);
  const newSet = new Set(newWords);

  return (
    <table className="w-full border-collapse" style={{ fontSize: "12px" }}>
      <thead>
        <tr>
          <th className="border p-1 bg-red-50 text-red-700 font-bold">OLD VERSION</th>
          <th className="border p-1 bg-emerald-50 text-emerald-700 font-bold">NEW VERSION</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td className="border p-2 align-top bg-red-50" style={{ width: "50%" }}>
            <div className="line-through">
              {oldWords.map((w, i) => (
                <span
                  key={i}
                  className={!newSet.has(w) ? "bg-red-200 text-red-900 px-0.5" : ""}
                >
                  {w}
                </span>
              ))}
            </div>
          </td>
          <td className="border p-2 align-top bg-emerald-50" style={{ width: "50%" }}>
            <div>
              {newWords.map((w, i) => (
                <span
                  key={i}
                  className={!oldSet.has(w) ? "bg-emerald-200 text-emerald-900 px-0.5" : ""}
                >
                  {w}
                </span>
              ))}
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  );
}

function ExpandableText({ text, maxLength = 200 }: { text: string | null | undefined; maxLength?: number }) {
  const raw = normalizeText(text);
  const [expanded, setExpanded] = useState(false);
  const isLong = raw.length > maxLength;
  const shown = expanded ? raw : raw.slice(0, maxLength) + (isLong ? "..." : "");

  return (
    <div>
      <div className="text-xs text-gray-700 whitespace-pre-wrap">
        {raw || <span className="text-gray-400 italic">ไม่มีข้อมูล</span>}
      </div>
      {isLong && (
        <button
          type="button"
          onClick={() => setExpanded(!expanded)}
          className="mt-1 text-xs font-medium text-blue-600 hover:text-blue-800"
        >
          {expanded ? "[ย่อข้อความ]" : "[อ่านต่อ...]"}
        </button>
      )}
    </div>
  );
}

/* ================= MAIN PAGE ================= */

export default function CompareReportPage() {
  const { id } = useParams<{ id: string }>();
  const [detail, setDetail] = useState<ComparisonDetail | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState("");
  const [riskFilter, setRiskFilter] = useState<"ALL" | RiskLevel>("ALL");
  const [typeFilter, setTypeFilter] = useState<"ALL" | ChangeType>("ALL");
  const [annotating, setAnnotating] = useState(false);
  const [chatById, setChatById] = useState<Record<number, ChatState>>({});

  useEffect(() => {
    if (id) loadDetail();
  }, [id]);

  const loadDetail = async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`${API_BASE}/comparisons/${id}`, { cache: "no-store" });
      if (!res.ok) throw new Error(`Failed to load (${res.status})`);
      const data: ComparisonDetail = await res.json();
      setDetail(data);

      // Initialize chat states
      setChatById(prev => {
        const next = { ...prev };
        for (const ch of data.changes || []) {
          if (!next[ch.id]) {
            next[ch.id] = {
              open: false,
              mode: "risk",
              question: "",
              loading: false,
              answer: "",
              error: null,
            };
          }
        }
        return next;
      });
    } catch (err: any) {
      setError(err?.message || "โหลดข้อมูลไม่สำเร็จ");
    } finally {
      setLoading(false);
    }
  };

  const handleAnnotate = async () => {
    if (!id) return;
    setAnnotating(true);
    try {
      const res = await fetch(`${API_BASE}/comparisons/${id}/annotate`, { method: "POST" });
      if (!res.ok) throw new Error(`Annotation failed (${res.status})`);
      await loadDetail();
    } catch (err: any) {
      console.error("Annotation error:", err);
    } finally {
      setAnnotating(false);
    }
  };

  const toggleChat = (changeId: number) => {
    setChatById(prev => ({
      ...prev,
      [changeId]: {
        ...(prev[changeId] || {
          open: true,
          mode: "risk",
          question: "",
          loading: false,
          answer: "",
          error: null,
        }),
        open: !(prev[changeId]?.open ?? false),
        error: null,
      },
    }));
  };

  const sendChat = async (changeId: number) => {
    const st = chatById[changeId];
    const question = normalizeText(st?.question);
    const mode = st?.mode || "risk";

    if (!question) return;

    setChatById(prev => ({
      ...prev,
      [changeId]: { ...prev[changeId], loading: true, error: null },
    }));

    try {
      const res = await fetch(`${API_BASE}/changes/${changeId}/chat`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ question, mode }),
      });

      if (!res.ok) throw new Error(`Chat failed (${res.status})`);
      const data = await res.json();

      setChatById(prev => ({
        ...prev,
        [changeId]: {
          ...prev[changeId],
          loading: false,
          answer: normalizeText(data?.answer) || "",
          error: null,
        },
      }));
    } catch (e: any) {
      setChatById(prev => ({
        ...prev,
        [changeId]: {
          ...prev[changeId],
          loading: false,
          error: e?.message || "ส่งคำถามไม่สำเร็จ",
        },
      }));
    }
  };

  const filteredChanges = useMemo(() => {
    const changes = detail?.changes || [];
    const query = searchQuery.trim();

    return changes.filter(c => {
      if (riskFilter !== "ALL") {
        const lv = (c.risk_level || "LOW").toUpperCase();
        if (lv !== riskFilter) return false;
      }

      if (typeFilter !== "ALL") {
        if (c.change_type !== typeFilter) return false;
      }

      if (query.length > 0) {
        const hay = [
          c.section_label,
          c.old_text,
          c.new_text,
          c.ai_comment,
          c.ai_suggestion,
          c.risk_reason,
          c.change_type,
          c.risk_level,
        ]
          .map(x => normalizeText(x as any))
          .join(" | ");

        if (!contains(hay, query)) return false;
      }

      return true;
    });
  }, [detail?.changes, searchQuery, riskFilter, typeFilter]);

  const riskSummary = useMemo(() => {
    const changes = detail?.changes || [];
    return {
      HIGH: changes.filter(c => c.risk_level === "HIGH").length,
      MEDIUM: changes.filter(c => c.risk_level === "MEDIUM").length,
      LOW: changes.filter(c => c.risk_level === "LOW").length,
      TOTAL: changes.length,
    };
  }, [detail?.changes]);

  if (loading) {
    return (
      <div className="p-4">
        <div className="text-center py-10">
          <ArrowPathIcon className="h-8 w-8 animate-spin text-blue-600 mx-auto" />
          <p className="mt-3 text-gray-700">กำลังโหลดรายงาน...</p>
        </div>
      </div>
    );
  }

  if (error || !detail) {
    return (
      <div className="p-4">
        <div className="bg-white border border-red-200 rounded p-6 max-w-md mx-auto">
          <ExclamationTriangleIcon className="h-10 w-10 text-red-500 mx-auto" />
          <h2 className="text-lg font-bold text-gray-900 mt-3 text-center">ไม่พบรายงาน</h2>
          <p className="text-gray-600 mt-1 text-center">{error || "ไม่พบข้อมูลการเปรียบเทียบนี้"}</p>
          <button
            onClick={loadDetail}
            className="mt-4 w-full px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 font-medium"
          >
            โหลดใหม่
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="p-4 font-sans">
      {/* Header */}
      <div className="mb-6">
        <div className="bg-white border border-gray-300 rounded p-4 mb-3">
          <div className="flex justify-between items-start">
            <div>
              <h1 className="text-xl font-bold text-gray-900 mb-1">{detail.document_name}</h1>
              <div className="flex items-center gap-2 text-sm text-gray-600 mb-2">
                <span className="px-2 py-0.5 bg-blue-100 text-blue-700 rounded font-medium">
                  {detail.version_old_label}
                </span>
                <span>→</span>
                <span className="px-2 py-0.5 bg-green-100 text-green-700 rounded font-medium">
                  {detail.version_new_label}
                </span>
                <span className="text-gray-400">•</span>
                <span>{formatDateTime(detail.created_at)}</span>
                <span className="text-gray-400">•</span>
                <span>ID: {detail.id}</span>
              </div>
            </div>
            <button
              onClick={handleAnnotate}
              disabled={annotating}
              className="flex items-center gap-1 px-3 py-1.5 bg-purple-600 text-white text-sm rounded hover:bg-purple-700 disabled:opacity-50"
            >
              {annotating ? (
                <ArrowPathIcon className="h-3.5 w-3.5 animate-spin" />
              ) : (
                <SparklesIcon className="h-3.5 w-3.5" />
              )}
              {annotating ? "กำลังวิเคราะห์..." : "AI วิเคราะห์"}
            </button>
          </div>

          {/* Risk Summary */}
          <div className="mt-3 pt-3 border-t border-gray-200">
            <div className="flex items-center gap-4 text-sm">
              <div>
                <span className="text-gray-600">ความเสี่ยงรวม: </span>
                <span className={`font-bold ${
                  detail.overall_risk_level?.includes("HIGH") ? "text-red-600" :
                  detail.overall_risk_level?.includes("MEDIUM") ? "text-amber-600" :
                  "text-emerald-600"
                }`}>
                  {detail.overall_risk_level || "ไม่ระบุ"}
                </span>
              </div>
              <div className="flex items-center gap-3">
                <span className="text-gray-600">สรุปความเสี่ยง:</span>
                <span className="px-2 py-0.5 bg-red-100 text-red-700 text-xs rounded font-bold">HIGH: {riskSummary.HIGH}</span>
                <span className="px-2 py-0.5 bg-amber-100 text-amber-700 text-xs rounded font-bold">MEDIUM: {riskSummary.MEDIUM}</span>
                <span className="px-2 py-0.5 bg-emerald-100 text-emerald-700 text-xs rounded font-bold">LOW: {riskSummary.LOW}</span>
                <span className="px-2 py-0.5 bg-blue-100 text-blue-700 text-xs rounded font-bold">TOTAL: {riskSummary.TOTAL}</span>
              </div>
            </div>
          </div>
        </div>

        {/* AI Summary */}
        {detail.summary_text && (
          <div className="bg-white border border-gray-300 rounded p-4 mb-4">
            <div className="flex items-center gap-2 mb-2">
              <DocumentMagnifyingGlassIcon className="h-4 w-4 text-blue-600" />
              <h2 className="text-sm font-bold text-gray-900">สรุปผลการวิเคราะห์</h2>
            </div>
            <div className="text-sm text-gray-700 bg-blue-50 border border-blue-100 rounded p-3">
              {detail.summary_text}
            </div>
          </div>
        )}
      </div>

      {/* Filters */}
      <div className="bg-white border border-gray-300 rounded p-3 mb-4">
        <div className="flex flex-col md:flex-row md:items-center gap-2 mb-2">
          <div className="flex-1 flex items-center gap-2">
            <MagnifyingGlassIcon className="h-4 w-4 text-gray-400" />
            <input
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="ค้นหาคำในรายการเปลี่ยนแปลง..."
              className="flex-1 px-3 py-1.5 text-sm border border-gray-300 rounded focus:outline-none focus:border-blue-500"
            />
          </div>
          <div className="flex items-center gap-2">
            <FunnelIcon className="h-4 w-4 text-gray-400" />
            <select
              value={riskFilter}
              onChange={(e) => setRiskFilter(e.target.value as any)}
              className="px-2 py-1.5 text-sm border border-gray-300 rounded bg-white"
            >
              <option value="ALL">ความเสี่ยงทั้งหมด</option>
              <option value="HIGH">HIGH</option>
              <option value="MEDIUM">MEDIUM</option>
              <option value="LOW">LOW</option>
            </select>
            <select
              value={typeFilter}
              onChange={(e) => setTypeFilter(e.target.value as any)}
              className="px-2 py-1.5 text-sm border border-gray-300 rounded bg-white"
            >
              <option value="ALL">ประเภททั้งหมด</option>
              <option value="ADDED">ADDED</option>
              <option value="REMOVED">REMOVED</option>
              <option value="MODIFIED">MODIFIED</option>
            </select>
          </div>
        </div>
        <div className="text-xs text-gray-600">
          แสดง {filteredChanges.length} จาก {detail.changes.length} รายการ
        </div>
      </div>

      {/* Changes List - แนวตั้ง */}
      <div className="space-y-4">
        {filteredChanges.length === 0 ? (
          <div className="bg-white border border-gray-300 rounded p-6 text-center">
            <p className="text-gray-500">ไม่พบรายการเปลี่ยนแปลงตามเงื่อนไขที่เลือก</p>
          </div>
        ) : (
          filteredChanges.map((change) => {
            const chat = chatById[change.id] || {
              open: false,
              mode: "risk" as AiMode,
              question: "",
              loading: false,
              answer: "",
              error: null,
            };

            return (
              <div key={change.id} className="bg-white border border-gray-300 rounded overflow-hidden">
                {/* Header */}
                <div className="border-b border-gray-200 p-3 bg-gray-50">
                  <div className="flex justify-between items-start mb-2">
                    <div className="flex items-center gap-2">
                      {changeTypeBadge(change.change_type)}
                      <span className="font-bold text-gray-900 text-sm">
                        {change.section_label || "ไม่มีหัวข้อ"}
                      </span>
                    </div>
                    <div className="flex items-center gap-2">
                      {riskBadge(change.risk_level)}
                      <span className="text-xs text-gray-500">ID: {change.id}</span>
                    </div>
                  </div>
                </div>

                {/* Content - แนวตั้ง */}
                <div className="p-3">
                  {/* Diff View */}
                  <div className="mb-4">
                    <div className="text-xs font-bold text-gray-700 mb-1">การเปลี่ยนแปลงข้อความ</div>
                    <DiffView
                      oldText={normalizeText(change.old_text)}
                      newText={normalizeText(change.new_text)}
                    />
                  </div>

                  {/* AI Comment */}
                  <div className="mb-3">
                    <div className="flex justify-between items-center mb-1">
                      <div className="text-xs font-bold text-gray-700">ความคิดเห็นจาก AI</div>
                      <CopyButton text={normalizeText(change.ai_comment)} />
                    </div>
                    <div className="bg-blue-50 border border-blue-100 rounded p-2 text-xs">
                      <ExpandableText text={change.ai_comment} maxLength={150} />
                    </div>
                  </div>

                  {/* AI Suggestion */}
                  <div className="mb-3">
                    <div className="flex justify-between items-center mb-1">
                      <div className="flex items-center gap-1 text-xs font-bold text-gray-700">
                        <LightBulbIcon className="h-3 w-3" />
                        คำแนะนำจาก AI
                      </div>
                      <CopyButton text={normalizeText(change.ai_suggestion)} />
                    </div>
                    <div className="bg-amber-50 border border-amber-100 rounded p-2 text-xs">
                      <ExpandableText text={change.ai_suggestion} maxLength={150} />
                    </div>
                  </div>

                  {/* Risk Reason */}
                  {normalizeText(change.risk_reason) && (
                    <div className="mb-3">
                      <div className="text-xs font-bold text-gray-700 mb-1">เหตุผลความเสี่ยง</div>
                      <div className="text-xs text-gray-600 bg-gray-50 border border-gray-200 rounded p-2">
                        {change.risk_reason}
                      </div>
                    </div>
                  )}

                  {/* AI Chat */}
                  <div className="border-t border-gray-200 pt-3 mt-3">
                    <button
                      onClick={() => toggleChat(change.id)}
                      className="flex items-center gap-1 px-3 py-1.5 text-xs bg-white border border-gray-300 rounded hover:bg-gray-50 text-gray-700"
                    >
                      <ChatBubbleLeftRightIcon className="h-3.5 w-3.5" />
                      ถาม AI เพิ่มเติม
                      <span className="text-xs">{chat.open ? "▲" : "▼"}</span>
                    </button>

                    {chat.open && (
                      <div className="mt-3 p-3 bg-gray-50 border border-gray-200 rounded">
                        <div className="mb-2">
                          <label className="text-xs font-bold text-gray-700 mb-1 block">โหมด:</label>
                          <select
                            value={chat.mode}
                            onChange={(e) =>
                              setChatById(prev => ({
                                ...prev,
                                [change.id]: { ...prev[change.id], mode: e.target.value as AiMode },
                              }))
                            }
                            className="w-full px-2 py-1 text-xs border border-gray-300 rounded bg-white"
                          >
                            <option value="risk">วิเคราะห์ความเสี่ยง</option>
                            <option value="short">ตอบแบบสั้น</option>
                            <option value="detailed">ตอบแบบละเอียด</option>
                            <option value="legal">เชิงกฎหมาย</option>
                          </select>
                        </div>

                        <textarea
                          value={chat.question}
                          onChange={(e) =>
                            setChatById(prev => ({
                              ...prev,
                              [change.id]: { ...prev[change.id], question: e.target.value },
                            }))
                          }
                          placeholder="พิมพ์คำถามเกี่ยวกับการเปลี่ยนแปลงนี้..."
                          rows={2}
                          className="w-full px-2 py-1 text-xs border border-gray-300 rounded focus:outline-none focus:border-blue-500 mb-2"
                        />

                        {chat.error && (
                          <div className="text-xs text-red-600 bg-red-50 border border-red-200 rounded p-1 mb-2">
                            {chat.error}
                          </div>
                        )}

                        <div className="flex gap-2">
                          <button
                            onClick={() => sendChat(change.id)}
                            disabled={chat.loading || !chat.question.trim()}
                            className="px-3 py-1 text-xs bg-blue-600 text-white rounded hover:bg-blue-700 disabled:opacity-50"
                          >
                            {chat.loading ? "กำลังส่ง..." : "ส่งคำถาม"}
                          </button>
                          <CopyButton text={chat.answer} label="คัดลอกคำตอบ" size="sm" />
                        </div>

                        {chat.answer && (
                          <div className="mt-3 p-2 bg-white border border-gray-300 rounded">
                            <div className="text-xs font-bold text-gray-700 mb-1">คำตอบจาก AI</div>
                            <div className="text-xs text-gray-600 whitespace-pre-wrap">{chat.answer}</div>
                          </div>
                        )}
                      </div>
                    )}
                  </div>

                  {/* Quick Copy */}
                  <div className="mt-3 pt-3 border-t border-gray-200">
                    <CopyButton
                      text={`[${change.section_label || "-"}]\n` +
                        `ประเภท: ${change.change_type} | ความเสี่ยง: ${change.risk_level}\n\n` +
                        `OLD:\n${change.old_text || "-"}\n\n` +
                        `NEW:\n${change.new_text || "-"}\n\n` +
                        `AI COMMENT:\n${change.ai_comment || "-"}\n\n` +
                        `AI SUGGESTION:\n${change.ai_suggestion || "-"}`
                      }
                      label="คัดลอกทั้งหมด"
                      size="md"
                    />
                  </div>
                </div>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
}